<script>
import Chart from 'chart.js'
// import $ from 'jquery'
var chart;

export default {
  props: {
    header: String
  },
  data(){
    return{
      chartData : [],
      colorData : [0.7,0.3,0.1,0,-0.1,-0.2,-0.7,-1],
      rangeData : [
        {id:"range01",name:"USD Change",value:5},
        {id:"range02",name:"EUR Change",value:-2},
        {id:"range03",name:"BCI 企業信心指數",value:0},
        {id:"range04",name:"CCI 消費者信心指數",value:-5}
      ],
      tableData01 : [
        ["week01",442,338,1.14],
        ["week02",244,239,0.84],
        ["week03",407,261,1.56],
        ["week04",482,439,1.10],
        ["week05",476,471,1.01]
      ],
      tableData02 : [
        ["AMD StockP", -0.1],
        ["Avgellin per Country", 0.3],
        ["BIC OECDE", 1.15],
        ["BranchStock excl OTW", -0.2],
        ["BranchStock OTW", -0.7]
      ],
      dataChangeBtnClass: ["active","","",""]
    }
  },
  mounted:function(){
    this.init() 
  },
  methods:{
    init(){
      if(chart){
        chart.destroy();
        console.log("!!!");
        
      }
      this.chartData = [
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        this.randomData(),
        NaN,
        NaN
      ]
      var ctx = document.getElementById('myChart');
      chart = new Chart(ctx, {
          type: 'line',
          data: {
              labels: ['week01','week02','week03','week04','week05','week06','week07','week08','week09','week10','week11','week12'],
              datasets: [{
                  label: 'SellinQty',
                  data: this.chartData,
                  backgroundColor: [
                      'rgba(255, 99, 132, 0)'
                  ],
                  borderColor: [
                      '#06fce7'
                  ],
                  borderWidth: 2,
                  lineTension: 0 //使用折線 不要曲線
              },
              {
                  label: 'FCST',
                  data: [NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,this.chartData[9],this.randomData(),this.randomData()],
                  backgroundColor: [
                      'rgba(255, 206, 86, 0)'
                  ],
                  borderDash: [5,5],
                  borderColor: [
                      '#fd3aa8'
                  ],
                  borderWidth: 2,
                  lineTension: 0 //使用折線 不要曲線
              },{
                  label: 'FCST模擬',
                  data: [NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,NaN,this.chartData[9],this.randomData(),this.randomData()],
                  backgroundColor: [
                      'rgba(255, 206, 86, 0)'
                  ],
                  borderDash: [5,5],
                  borderColor: [
                      '#fff'
                  ],
                  borderWidth: 2,
                  lineTension: 0 //使用折線 不要曲線
              }]
          },
          options: {
              scales: {
                  yAxes: [{
                      ticks: {
                          beginAtZero: true
                      }
                  }]
              }
          }
      });
    },
    rangeChange(event){
      const value = event.target.value;
      const id = event.target.dataset.index;
      this.rangeData[id].value = value;
    },
    randomData(){
      return Math.floor((Math.random() * 10) + 1);
    },
    changeChartData(event){
      const value = event.target.value;
      var _data1 = "",_data2 = "",_data3 = "",_data4 = "";
      if(value == 1)_data1 = "active";
      if(value == 2) _data2 = "active";
      if(value == 3) _data3 = "active";
      if(value == 4) _data4 = "active";
      this.dataChangeBtnClass = [_data1,_data2,_data3,_data4];
    
      this.init();
      
    },
    rangeBgColor(value,index){
      switch(index){
        case 1:
          if(value < -4) return "active"
          break;
        case 2:
          if(value < -3) return "active"
          break;
        case 3:
          if(value < -2) return "active"
          break;
        case 4:
          if(value < -1) return "active"
          break;
        case 5:
          if(value < 0) return "active"
          break;
        case 6:
          if(value > 0) return "active"
          break;
        case 7:
          if(value > 1) return "active"
          break;
        case 8:
          if(value > 2) return "active"
          break;
        case 9:
          if(value > 3) return "active"
          break;
        case 10:
          if(value > 4) return "active"
          break;
      }
    },
    setColor(num){
      var r = 255;
      var g = 255;
      var b = 255;
      var text = "#fff";

      if(num>0){
        r = 255 - (255 * num)
      }else if(num<=0){
        g = 255 - (255 * (-num))  
      }
      b = g / 2;
      r = parseInt(r);
      g = parseInt(g);
      b = parseInt(b);
      
      return "background-color: rgba(" + r + "," + g + "," + b + ",0.5);color:"+text;
    }
  }
}
</script>

